/*
===============================================================================================================
------------------------------------------------ Шрифт «Inter» ------------------------------------------------
===============================================================================================================
*/

// *** жирность «Regular» ***
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  font-display: swap;

  src:
    local("Inter Regular"),
    local("Inter-Regular"),
    url("../fonts/Inter-Regular.woff2") format("woff2"),
    url("../fonts/Inter-Regular.woff") format("woff");

  unicode-range: U+0400-045F, U+0490-0491, U+04B0-04B1, U+2116;
}


// *** жирность «Medium» ***
@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-display: swap;

  src:
    local("Inter Medium"),
    local("Inter-Medium"),
